import React from "react";
import { Redirect } from "react-router";
import client from "./CLASSES/client";
import Pure from "./Pure";

class Building extends Pure {
    id = parseInt(new URLSearchParams(window.location.search).get("id"));
    valid = isNaN(this.id) ? false : (this.id > 0 && this.id < 41);
    render() {
        if (!client.ready)
            return <>...</>;
        else if (!client.authorized) {
            console.log("Not Authorized to view building");
            return <Redirect to="/Home" />;
        }
        else if (this.valid)
            return <BuildingView id={this.id} />;
        else
            return <Redirect to="/Village" />;
    }
}

class BaseView extends Pure {
    level = 0;
    buildingId = 0;
    placeId = 0;
    constructor(props) {
        super(props);
        this.level = this.props.level;
        this.placeId = this.props.placeId;
        this.buildingId = this.props.buildingId;
    }
}

class BuildingView extends BaseView {
    // constructor(props) {
    //     super(props);
    // }
    render() {
        return <>مبنى {this.props.id}</>
    }
}

export default Building;